*{margin:0;padding:0;box-sizing:border-box;font-family:'Roboto';font-size:14px}body{padding:10px;background:radial-gradient(37.2% 50% at 50% 50%, #ECECEC 0%, #C4C4C4 100%)}.order{margin:227px auto 0;padding-top:20px;width:650px;max-width:100%;min-height:345px;background:radial-gradient(138.81% 16453.56% at -23.78% 12.1%, #3C8291 9.59%, #00B5AD 100%);text-align:center;box-shadow:0px 2px 10px rgba(0,0,0,0.25);border-radius:6px;overflow:hidden}.order__heading{margin-bottom:20px;font-size:1.8em;color:#fff}@media screen and (max-width: 800px){.order{margin:0 auto}}.form{display:grid;grid-template-columns:48% 48%;grid-gap:20px;gap:20px;width:100%;min-height:300px;padding:45px;background-color:#fff;text-align-last:left}.form__about{margin-bottom:15px}.form__forgot{cursor:pointer}.form__info{min-height:130px;grid-column:auto / span 2;font-size:1.2em;line-height:1.4;text-align:left}.form__heading{display:block;width:200px;margin:30px auto 60px;font-size:1.75em}.form--ready{display:block;text-align:center}.form--ready button{margin:0 auto}@media screen and (max-width: 500px){.form{padding:35px 15px 15px;grid-template-columns:100%}.form__info{grid-column:auto}}

